@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=M+PLUS+1p:wght@300&family=Noto+Sans+JP&display=swap);
* {
    margin: 0;
    padding: 0;
}

html {
  font-size: 10px;
}
body {
  font-size: 1.6rem;
}
h1{
  font-size: 5rem;
}
p {
  font-size: 2.5rem;
}

@media screen and (max-width: 640px) {
    html {
      font-size: 7px;
    }
}
:root {
    --main-light-bg-color:#f4f5f7;
    --main-dark-bg-color: #282c34;
    --main-light-font-color: white;
    --main-light-theme-color: #00e091;
    --main-dark-font-color: #282c34;
    --main-dark-theme-color: #1aac5d;
}

.App {
    text-align: center;
    background-color: #f4f5f7;
    background-color: var(--main-light-bg-color);
    height: 100vh;
}

body {
    font-family: "Noto Sans JP", sans-serif;
}

.dark-theme-color {
  color: #1aac5d;
  color: var(--main-dark-theme-color);
}

.light-theme-color {
  color: #00e091;
  color: var(--main-light-theme-color);
}

.section-body {
    width: 80vw;
    min-width: 60em;
    margin: auto;
    margin-top: 4em;
    text-align: left;
    padding: 0 2em;
}

.section-image {
    height: 20em;
    display: block;
    margin: auto;
    border-radius: 5%;
    margin-bottom: 2em;
    box-shadow: 0.1em 0.1em 2em;
}
.Headline {
    --headline-spacing: 0.8rem;
    font-family: "Lato", sans-serif;
}

.headline-body {
    margin: 0;
    display: inline-block;
    font-size: 6em;
    text-align: center;
    letter-spacing: var(--headline-spacing);
    vertical-align: middle;
}

.headline-fontawesome {
    margin-right: var(--headline-spacing);
}

.headline-image {
    width: 0.8em;
    margin: 0;
    margin-right: var(--headline-spacing);
    border-radius: 30%;
    border: var(--main-dark-bg-color) 1px solid;
    vertical-align: middle;
}
.List {
    margin: 0;
    padding: 0;
}

.list-fontawesome {
    margin-right: 1rem;
}
